/**
 * 
 * 
 * baseUrl: 
 * routerMode: 
 * imgBaseUrl: 
 * 
 */
let baseUrl;
let routerMode;
const imgBaseUrl = 'https://img.you-xian.com';

if (process.env.NODE_ENV == 'development') {
    baseUrl = 'http://localhost:8888';
    routerMode = 'hash'
} else {
    baseUrl = 'http://you-xian.com';
    routerMode = 'hash'
}

export {
    baseUrl,
    routerMode,
    imgBaseUrl
}


